			headlines: [
				"苹果XR对比华为Mate20你会选择谁",
				"格兰仕暗示拜访拼多多后遭天猫打压，拼多多高层赞其有勇气",
				"耐克没进前十，今年Q1全球受欢迎品牌榜"
			],
		}

	},
	onLoad(){
		this.carousel();
